import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class BreakpointObserverService {


  isHandset$: Observable<boolean>;
  isMobile$: Observable<boolean>;
  isLarge$: Observable<boolean>;
  constructor(private breakpointObserver: BreakpointObserver) {
    this.isHandset$ = this.breakpointObserver.observe([Breakpoints.Medium, Breakpoints.Large, Breakpoints.XLarge])
      .pipe(
        map(result => !result.matches),
        shareReplay(1)
      );

    this.isMobile$ = this.breakpointObserver.observe([
      Breakpoints.Handset
    ]).pipe(
      map(result => result.matches),
      shareReplay(1)
    );

    this.isLarge$ = this.breakpointObserver.observe([
      Breakpoints.Large,
      Breakpoints.XLarge
    ]).pipe(
      map(result => result.matches),
      shareReplay(1)
    );
  }

  getIsHandset$() {
    return this.isHandset$;
  }

  getIsMobile$() {
    return this.isMobile$;
  }

  getIsLarge$() {
    return this.isLarge$;
  }
}
