import { Component, ViewChild } from '@angular/core';
import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';
import { Observable } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';
import { MatSidenav } from '@angular/material/sidenav';
import { BreakpointObserverService } from 'src/app/utils/breakpoint-observer.service';

@Component({
  selector: 'app-navigation',
  templateUrl: './navigation.component.html',
  styleUrls: ['./navigation.component.scss']
})
export class NavigationComponent {

  @ViewChild('drawer', { static: false, read: MatSidenav }) drawer: MatSidenav | undefined;

  isHandset$: Observable<boolean> = this.breakpointObserver.getIsHandset$();

  constructor(
    private breakpointObserver: BreakpointObserverService
  ) { }

  public toggleDrawer(): void {
    if (this.drawer) {
      this.drawer.toggle();
    }
  }

}
