import { Component, OnInit } from '@angular/core';
import { startOfYear } from 'date-fns';
import { EChartsOption, EChartsType } from 'echarts';

@Component({
  selector: 'app-line-chart',
  templateUrl: './line-chart.component.html',
  styleUrls: ['./line-chart.component.scss']
})
export class LineChartComponent implements OnInit {

  public echartsInstance: EChartsType | null;
  legendItem = {
    item: 'square'
  };

  chartOption: EChartsOption = {
    grid: {
      left: '5%',
      right: 130
    },
    xAxis: {
      type: 'category',
      data: ['2018/3', '2018/6', '2018/9', '2018/12', '2019/3', '2019/6', '2019/9'],
      boundaryGap: false,
      splitLine: {
        show: true,
        interval: 'auto',
        lineStyle: {
          color: '#dddddd'
        }
      },
      axisLine: {
        lineStyle: {
          color: '#dddddd'
        }
      },
      axisLabel: {
        color: '#888888'
      },
      axisTick: {
        show: false,
      }
    },
    yAxis: {
      type: 'value',
      name: '(百萬)',
      nameLocation: 'end',
      nameTextStyle: {
        align: 'right'
      },
      splitLine: {
        show: true,
        interval: 'auto',
        lineStyle: {
          color: '#dddddd',
          type: 'dashed',
        }
      },
      axisLabel: {
        formatter: (value: number) => {
          if (!value && value !== 0) { return '0'; }

          const str = value.toString();
          const reg =
            str.indexOf('.') > -1 ? /(\d)(?=(\d{3})+\.)/g : /(\d)(?=(?:\d{3})+$)/g;
          return `$${str.replace(reg, '$1,')}`;
        }
      }
    },
    tooltip: {
      trigger: 'axis',
      axisPointer: {
        axis: 'x',
        lineStyle: {
          color: '#222222',
          type: 'solid'
        }
      },
      backgroundColor: 'rgba(0, 0, 0, 0.65)',
      textStyle: {
        color: '#ffffff'
      }
      // formatter: '{b}'
    },
    legend: {
      type: 'plain',
      orient: 'vertical',
      align: 'left',
      right: 10,
      top: 70,
      textStyle: {
        color: '#222222',
        fontSize: 14,
        fontFamily: 'NotoSansTC'
      },
      ...{
        icon: 'square'
      }
    },
    series: [
      {
        name: '營業收入',
        data: [
          820, 932, 901, 934, 1290, 1330, 1320],
        showSymbol: true,
        symbolSize: 8,
        type: 'line'
      },
      {
        name: '銷售成本',
        data: [
          420, 532, 401, 434, 290, 630, 420],
        showSymbol: true,
        symbolSize: 8,
        type: 'line'
      },
    ],
  };

  constructor() {
    this.echartsInstance = null;
  }

  ngOnInit(): void {
  }

  onChartInit(ec: EChartsType): void {
    this.echartsInstance = ec;
  }

  resizeChart(): void {
    if (this.echartsInstance) {
      this.echartsInstance.resize();
    }
  }

}
